@mixin container ($max-width, $padding:()) {
		max-width: $max-width;
		padding: $padding;
}
@mixin clearfix () {
		&:before,
		&:after {
				content: "";
				display: table;
		}
		&:after {
				clear: both;
		}
}
@mixin media($point) {
	@if $point == desktop-large {
	 @media only screen and (max-width: $desktop-large) { @content; }
	}
	@if $point == desktop {
	 @media only screen and (max-width: $desktop) { @content; }
	}
	@else if $point == mobile {
	 @media only screen and (max-width: $mobile) { @content; }
	}
}
